@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:ital,wght@1,300&family=Inter:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Black+Ops+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  
}




.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.homeTitle {
  font-family: "ProximaNova", sans-serif;
  font-style: normal;
  font-weight: bolder;
  font-size: 6rem;
  line-height: 96px;
  margin-bottom: 0%;
  letter-spacing: -8px;
}

.homesubtitle {
  font-family: "ProximaNova", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 5rem;
  /* line-height: 96px; */
  color: rgb(143, 137, 137);
  margin-top: 0%;
}

.cvTitle {
  font-family: "ProximaNova";
  font-style: normal;
  font-weight: bolder;
  font-size: 2rem;
  line-height: 35px;
  color: rgb(143, 137, 137);
  margin-top: 0%;
  padding-bottom: 0;
}

p {
  font-family: "ProximaNova", monospace;
  font-size: 1rem;
  word-spacing: 8px;
  font-weight: 100;
  /* color: white; */
}

@font-face {
  font-family: "ProximaNova";
  src: url(/work-porfolio/static/media/FontsFree-Net-Proxima-Nova-Light.367531d5.otf) format("opentype");
}

.optionNumbers {
  font-family: "Black Ops One";
  font-size: 2rem;
}

.optionNames {
  font-family: "Black Ops One";
  font-size: 1rem;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  display: flex;
}

.monkeyAstronaut {
  -webkit-filter: drop-shadow(5px 5px 5px #222);
  filter: drop-shadow(5px 5px 5px #222);
}

